import React from 'react';

// 单行布局支持响应式 自由布局不支持
export const BreadcrumbContext = React.createContext<{ mode: 'singleLine' | 'free' }>({ mode: 'free'  });

export const SingleLineRowContext = React.createContext<{ 
    isSingleLine: boolean, ellipisisIndex?: number, ellipisisWidth?: number
}>({isSingleLine: false, ellipisisIndex: -1, ellipisisWidth: -1 });